export default {
	components: {
	},
	data() {
		return {
			token: '1',
			isAlert: false,
			isClear:false,
			avatarUrl: '',
			userName: ''
		};
	},
	methods: {
		fav(){
			uni.navigateTo({
				url:'/pages/loading/index'
			})
		},
		login(){
			let that=this
			uni.getUserInfo({
					provider: 'weixin',
					lang: 'zh_CN',
					success: res => {
						console.log(res)
						uni.setStorage({
							key: 'userInfo',
							data: res.userInfo,
						})
						this.avatarUrl=res.userInfo.avatarUrl
						this.userName=res.userInfo.nickName
					}
				})
		},
		tel(){
			uni.makePhoneCall({
				phoneNumber:'15183233274'
			})
		},
		info(){
			uni.navigateTo({
				url:'/pages/mine/info'
			})
		},
	},
	onLoad(options) {
		let user = uni.getStorageSync('userInfo')||''
		this.avatarUrl=user.avatarUrl
		this.userName=user.nickName
	}
};
